.container {
	height: 100vh;
	width: 100%;
	margin: 0;
	padding: 0;
	position: fixed;
}

.screen {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
	justify-content: center;
	display: flex;
	justify-content: start;
	align-items: center;
	transition: opacity 0.5s ease-out;
	z-index: 999;
}